<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <ctitle :title="'信访量分布'"></ctitle>
        <div class="detail">
            <allCount class="all-count" :count="jctotal"></allCount>
        </div>
        <div
          v-if="mapParams.options.JSON !== '440000'"
          class="map-back"
          @click="mapBack"
        ></div>
        <transition name="fade">
          <Map v-if="mapFlag" :params="mapParams" @change="handleMap" @showPopup='popShow'></Map>
        </transition>
      </div>
      <div class="c-right">
        <div class="c-r-t">
          <ctitle :title="'信访量走势'"></ctitle>
          <div class="inner">
            <LinesR :params="xflzsLineParams"></LinesR>
          </div>
        </div>
        <div class="c-r-c">
          <ctitle :title="'信访人群'"></ctitle>
          <div class="c-r-b">
            <div class="inner">
              <barline
                class="chart"
                :params="nlzbBarParams"
              ></barline>
              <p class="label">
                年龄分布
              </p>
            </div>
            <div class="inner">
              <loopPie :params="xbfbPieParams"></loopPie>
              <p class="label">
                性别分布
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--数据反查弹框-->
    <popLists v-if="popLists" :data="popLists" ref="popLists"></popLists>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
import ctitle from "@/components/title/index";
import typeChange from "@/components/typeChange/index";
import timeChange from "@/components/timeChange/index";
import LinesR from "@/components/line/LinesR";
import Map from "@/components/map/Map3";
import barline from "@/components/bar/BarLine";
import loopPie from "@/components/pie/LoopPie";
import allCount from "@/components/list/allCount";
import popLists from "@/components/popLists/index";

export default {
  name: "wtsd",
  props: {
    btnStatus: {
      type: String,
    }
  },
  components: {
    ctitle,
    typeChange,
    timeChange,
    LinesR,
    Map,
    barline,
    loopPie,
    allCount,
    popLists
  },
  data() {
    return {
      popLists: null,
      curCity: "广东省", // 默认广东省
      shijCity: "",
      shej_02: "invalid",
      shij_02: "invalid",
      xj_02: "invalid",
      nameP: "",
      mapJson: {
        广东省: "440000",
        广州市: "440100",
        韶关市: "440200",
        深圳市: "440300",
        珠海市: "440400",
        汕头市: "440500",
        佛山市: "440600",
        江门市: "440700",
        湛江市: "440800",
        茂名市: "440900",
        肇庆市: "441200",
        惠州市: "441300",
        梅州市: "441400",
        汕尾市: "441500",
        河源市: "441600",
        阳江市: "441700",
        清远市: "441800",
        东莞市: "441900",
        中山市: "442000",
        潮州市: "445100",
        揭阳市: "445200",
        云浮市: "445300"
      },
      // 性别分布
      xbfbPieParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          bgSize: 210,
          colors: this.$STANDARD.pieColors,
          radius: ["45%", "60%"],
          center: ["50%", "50%"],
          centerRadius: ["50%", "50%"],
          labelRich: {
            b: this.fontSize(0.15),
            d: this.fontSize(0.18),
            bHeight: this.fontSize(0.2)
          },
          length: 20,
          length2: 50,
          bRichPadding: [30, -50, 0, -50],
          borderWidth: 0,
          borderColor: "#0B2244",
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rich: {
            bfontsize: "10%" //文字大小
          },
          labelSize: "22%", // label字号
          threeLevel: true,
          rollFlag: true
        }
      },
      // 信访量分布地图
      mapParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          isHome: false,
          JSON: "440000",
          xName: "name",
          yName: "value",
          carousel: true,
          map: {}
        }
      },
      mapFlag: false,

      // 3年龄分布
      nlzbBarParams: {
        data: [],
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        options: {
          xName: "name", //x轴字段
          axisValueShow: true,
          splitLineShow: false,
          axis1Name: "人次",
          axisLabelSize: 12, // label字号
          left: "10%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          barWidth: 30,
          barLabelOptions: "top",
          barLabelShow: true, //柱状图数量值显示
          colors: {
            bar: [["#d2f69d", "#79cc7a"]]
          },
          map: {
            value: "数量"
          }
        }
      },
      curTypeId: "drop",
      //1地图左上角(件次总数)
      jctotal: 0,
      // 3信访量走势
      xflzsLineParams: {
        data: [],
        colors: [
          {
            line: "rgba(105, 65,141,0.7)",
            start: "rgba(105, 65,141,0.7)",
            end: "rgba(105, 65,141,0.7)"
          }
        ],
        option: {
          YaxisLineShow: true,
          splitNumber: 4,
          splitLineShow: false,
          legendShow: false,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisCategoryName: "日",
          axisYName: "件次",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: false,
          areaShow: 1,
          smoothMonotone: "none",
          dataZoomshow: false,
          legendIcon: "rect",
          left: "5%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          legendTop: "2%",
          legendRight: "10%"
        },
        canvasStyle: {
          width: "96%",
          height: "90%"
        },
        map: {
          value: "数量"
        },
        xName: "name"
      },
    };
  },
  mounted() {
    this.getMapAPI(this.date);
  },
  methods: {
    // 数据反查弹框
    popShow(obj) {
      let id = obj.id;
      // let id = obj.name;
      console.log(id);
      if (id) {
        get_http("popLists", {
          'value_id': id,
          'name': obj.name,
        })
        .then(res => {
          this.popLists = res.data;
          this.$nextTick(() => {
            this.$refs.popLists.popListsShow = true;
          })
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    // 4.全省信访量-地图
    getMapAPI(date) {
      // 处理接口参数
      // 省级
      if (this.curCity === "广东省") {
        this.shej_02 = this.curCity;
        this.shij_02 = "invalid";
        this.xj_02 = "invalid";
        this.nameP = "shij_02";
      } else {
        // 市级
        if (this.shijCity) {
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.xj_02 = "invalid";
          this.nameP = "xj_02";
        } else {
          // 县区级
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.xj_02 = this.curCity;
          this.nameP = "invalid";
        }
      }
      // 2.件次地图（地图）
      get_http("qjywjkWtsdXfbMap", {
        day: `[${date.startDate},${date.endDate}]`,
        gd_id3: this.btnStatus,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
        name: this.nameP,
      })
        .then(res => {
          this.mapParams.data = res.data.data;
          this.mapFlag = true;
          this.getAPI(date);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAPI(date) {
      // 1.信访量分布-件次总数（地图左上角）
      get_http("qjywjkWtsdXfbTotal", {
        day: `[${date.startDate},${date.endDate}]`,
        gd_id3: this.btnStatus,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
        full: true
      })
      .then(res => {
        this.jctotal = res.data.data[0].value;
      })
      .catch(err => {
        console.log(err);
      });
      // 2.信访量走势
      get_http("qjywjkWtsdXflzsLine", {
        day: `[${date.startDate},${date.endDate}]`,
        gd_id3: this.btnStatus,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.xflzsLineParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      // 3.年龄分布
      get_http("qjywjkWtsdNlzbPie", {
        day: `[${date.startDate},${date.endDate}]`,
        gd_id3: this.btnStatus,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.nlzbBarParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      
      // 4.性别分布
      get_http("qjywjkWtsdZbPie", {
        day: `[${date.startDate},${date.endDate}]`,
        gd_id3: this.btnStatus,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.xbfbPieParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      
    },
    // 点击地图下钻
    handleMap(name) {
      this.curCity = name;
      for (let key in this.mapJson) {
        if (this.curCity === key) {
          this.mapFlag = false;
          this.mapParams.options.JSON = this.mapJson[name];
        }
      }
    },
    // 地图返回全省
    mapBack() {
      this.mapFlag = false;
      this.curCity = "广东省";
      this.mapParams.options.JSON = "440000";
    },
    fontSize(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    curCity: function(newVal, oldVal) {
      if (newVal === "广东省") {
        this.getMapAPI(this.date);
      } else {
        let c = "";
        for (let key in this.mapJson) {
          if (newVal === key) {
            this.shijCity = newVal;
            c = newVal;
            this.getMapAPI(this.date);
          }
        }
        if (!c) {
          this.xj_02 = newVal;
          this.getAPI(this.date);
        }
      }
    },
    date(val) {
      this.getMapAPI(val);
    },
    btnStatus() {
      this.getMapAPI(this.date);
    }
  }
};
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .c-left
      position relative
      flex 1
      .map-back
        position absolute
        background url("~@/assets/back-icon.png") no-repeat
        top 30px
        right 60px
        width 30px
        height 30px
        z-index 1000
        font-size 0.2rem
        color #fff
        cursor pointer
      .map
        position absolute
        top -40px
        left 100px
      .fade-enter-active, .fade-leave-active
        transform scale(1)
        transition all 0.8s

      .fade-enter, .fade-leave-to
        opacity 0
        transform scale(0)
      .detail
        position absolute
        top 0.6rem
        left 1rem
        z-index 1000
        .all-count
          font-size 0.5rem
          >>>.number
            color #fff 
            text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
      .map-chart
        margin-top 0.1rem
        .top3
          .label
            float left
            width 0.5rem
            color #ffa800
            font-size 0.18rem
            font-weight 700
            line-height 0.4rem
          .list
            float left
            .item
              line-height 0.4rem
              font-size 0
              color #fff
              .order
                font-size 0.18rem
                margin-right 0.1rem
              .name
                display inline-block
                width 0.8rem
                font-size 0.18rem
              .number
                font-size 0.18rem
              .unit
                font-size 0.14rem
                color #5aceff
                margin-left 0.1rem
    .c-right
      flex 1
      display flex
      flex-direction column
      .c-r-t
        flex 1
        display flex
        flex-direction: column
        .m-title
          margin-left 0.1rem 0 0 0.6rem
        .inner
          flex 1
          margin-top 0.1rem
      .c-r-c
        flex 1
        display flex
        flex-direction column
        margin-top 0.3rem
        .m-title
          margin-bottom 0.1rem
        .c-r-b
          flex 1
          display flex
          flex-direction row
          .inner
            flex 1
            display flex
            flex-direction column
            .wrap
              margin-top 0.2rem
              display flex
              align-items center
              justify-content center
            .label
              font-size 0.2rem
              color #fff
              text-align center
              margin-bottom 0.1rem
</style>
